import React, { Component } from 'react';
import './App.css';
import { HorizontalBar } from 'react-chartjs-2';
import FullDataset from './FullDataset';

class App extends Component {

  constructor(props) {
    super(props);
    this.checkboxUsa = React.createRef();
    this.state = { fullDataSetObj: new FullDataset(), countryName: null, chart: null, DatanautsCount: 0, isUsaAdded: true };
  }

  componentDidMount() {
    this.showChart();
    this.checkboxUsa.current.checked = this.state.isUsaAdded;
  }

  renderize(arr) {
    if (arr.length === 0) return;

    const labels = [];
    const data = [];
    const backgroundColor = [];
    const colors = ['#FF6384', '#36A2EB', '#FFCE56'];
    let color = 0;
    const sortedArr = this.state.fullDataSetObj.sortByCount(arr);

    for (var i = 0; i < sortedArr.length; i++) {
      if (color === 3) color = 0;
      labels.push(sortedArr[i].name);
      data.push(sortedArr[i].count);
      backgroundColor.push(colors[color]);
      color++;
    }

    const chartData = {
      labels: labels,
      datasets: [{
        label: 'Datanauts',
        data: data,
        backgroundColor: backgroundColor,
        hoverBackgroundColor: backgroundColor,
        borderColor: 'rgba(255,99,132,1)',
        borderWidth: 1,
        hoverBorderColor: 'rgba(255,99,132,1)',
      }]
    };

    this.setState({ chart: chartData });
  }

  showChart(label) {
    const fullDataSetObj = this.state.fullDataSetObj;
    if (!label || this.state.countryName) {
      let arr = fullDataSetObj.dataset;
      if (!this.state.isUsaAdded) {
        arr = fullDataSetObj.getAsideFrom(arr, ['USA']);
      }
      this.renderize(arr);
      this.setState({ countryName: null, DatanautsCount: fullDataSetObj.getTotalCount(arr) });
    } else {
      if (!fullDataSetObj.hasCountryOnlyUnknownCity(label)) {
        const cities = fullDataSetObj.getCitiesByOwnerName(label);
        this.renderize(cities.arr);
        this.setState({ countryName: label, DatanautsCount: fullDataSetObj.getTotalCount(cities.arr) });
      }
    }
  }

  handleIncludeUsa() {
    const added = this.state.isUsaAdded;
    this.setState({ isUsaAdded: !added },
      () => {
        this.showChart();
      });
  }

  render() {
    const options = {
      legend: {
        display: false
      },
      scales: {
        xAxes: [
          {
            scaleLabel: {
              display: true,
              labelString: 'Number of Datanauts'
            },
            ticks: {
              autoSkip: true,
              min: 0
            }
          }
        ],

      },
      onClick: (e, item) => {
        if (item[0]) {
          const idx = item[0]._index;
          const label = this.state.chart.labels[idx];

          this.showChart(label);
        }
      }
    };

    const chart = this.state.chart ?
      <HorizontalBar data={this.state.chart} ref='chart'
        width={50}
        height={25}
        options={options}
      />
      :
      '';

    const title = this.state.countryName ?
      ''
      :
      <div>
        <input ref={this.checkboxUsa} type="checkbox" id="cbUsa" value="USA" onClick={() => { this.handleIncludeUsa() }} />
        <label htmlFor="cbUsa">Include the USA</label>
      </div>;

    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title">Searchnauts</h1>
        </header>
        <h2>{this.state.countryName}</h2>     
        <h4>{'Datanauts: ' + this.state.DatanautsCount}</h4>   
        <p id='pcbUsa'>{title}</p>
        {chart}
        
      </div>
    );
  }
}

export default App;



